.sideBar{
  flex: 3;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #d6d6d6;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: fit-content;
}
.sideBarItem {
   text-align: center;
   margin-top: 30px;
}
.sideBarItem> p{
   text-align:justify;
   padding: 15px;
   font-size: 12px;
}
.sideBarImg{
   width: 250px;
   align-items: center;
   margin-top: 10px;
}

.sideBarTitle{
 margin: 90px;
 padding: 5px;
 width: 80%;
 border-top: 1px solid #000;
 border-bottom: 1px solid #000;
 bottom: 10px;
 line-height: 20px;

 text-align: center;
 font-size: 12px;
 color: #222222;
 font-weight: 600;
}
ul.sideBarList {
   margin-top: 15px;
   list-style: none;
   margin-bottom: 10px;
}
.sideBarListItem{
   text-align: left;
   padding: 10px;
}

.sideBarSocial{
   text-align: center;
   margin-top: 10px;
} 
.sideBarIcon{
   margin-top: 10px;
   margin: 15px 15px 0px 15px;
   align-items: center;
}